import React from 'react'
import form_of_list from './form_of_list.json'
import './index.css'

class AddForm extends React.Component {
  render(){

    let style = {
      position: "absolute",
      backgroundColor: 'red'

    }

    return(
      <div className="AddForm" style={style}>add form</div>
    )
  }
}

class Element extends React.Component {

  state = {
    collapsed: false,
  }

  changeCollapsed = () => this.setState({collapsed: !this.state.collapsed})

  render(){

    let params = {
      marker1: '+',
      marker2: '-',
    }

    return(
      <div className="Element">
        <div style={{'display':'flex', 'justify-content': 'space-between'}}>
          <div className="Element_Title" onClick={()=>this.props.data ? this.changeCollapsed() : null} style={{'cursor': (this.props.data ? 'pointer' : 'default')}}>
            <div className="Element_Marker">{this.props.data ? (this.state.collapsed ? params.marker1 : params.marker2) : null}</div>
            <div className="Element_Text">{this.props.text}</div>
          </div>

        </div>
        <div className="Element_Data">
          {this.state.collapsed ? null : this.props.data}
        </div>
      </div>
    )
  }

}

class DropedList extends React.Component {


  isElementObj = ( element ) => typeof element === "object"

  createDataList = ( data ) => {
    let list = []

    Object.keys( data ).map( (key, i) => {
      if ( this.isElementObj( data[key].value) ) {
        list.push( <Element text={ data[key].title } data={ this.createDataList( data[key].value ) } key={i}/> )
      }
      else {
        list.push( <Element text={ data[key].title} key={i}/> )
      }
    })
    return list
  }

  render(){

    return(
      <div className='DropedList'>
        <div className="DropedList_Title">
          {this.props.title}
        </div>
        <div className="DropedList_Data">
          {this.createDataList( this.props.data )}
        </div>
      </div>
    )
  }
}

class JsonReader extends React.Component {

  state = {
    json: this.props.json
  }

  isElementObj = ( element ) => typeof element === 'object'

  String = ( element1, element2) => <tr><td>{element1}: </td><td>{element2}</td></tr>

  generateTable = (data) => {
    let list = []

    Object.keys( data ).map( (key, i) => {
      if ( this.isElementObj( data[key] ) ){
        list.push( this.String(key, this.generateTable(data[key])))
      }
      else {
        list.push( this.String(key, data[key] ))
      }
    })
    return list
  }

  render(){

    let table = this.generateTable( this.state.json )
    return(
      <div className={"JsonReader"}>
        <table>
          <tbody>
            {table}
          </tbody>
        </table>
      </div>
    )
  }
}

class App extends React.Component {

  state = {
    dropedListData: form_of_list,
    show_table: false,
    show_list: false
  }

  render(){
    return(
      <div className="App">
        <div className="Header">
            {"show list"}
            <input type="checkbox" onChange={()=>this.setState({show_list: !this.state.show_list})}></input>
            {"show table"}
            <input type="checkbox" onChange={()=>this.setState({show_table: !this.state.show_table})}></input>
        </div>
        <div style={{'display':"flex", "position":"absolute"}}>
          {this.state.show_list ? <DropedList data = {this.state.dropedListData} title="title"/> : null }
          {this.state.show_table ? <JsonReader json={this.state.dropedListData} /> : null }
        </div>
      </div>
    )
  }
}

export default App
